import ReactECharts from 'echarts-for-react'
import './style.scss';

export function Bar({ data }: any) {
    //console.log(data);

    let xAxisData = ["12 a 16 anos", "17 a 20 anos", "21 a 25 anos", "26 a 30 anos", "31 a 34 anos", "> 35 anos"];
    let data1 = [15, 20, 33, 45, 6, 16];
    let data2 = [100, 124, 230, 115, 164, 187];

    const options = {
        legend: {
            bottom: "0%"
        },
        xAxis: {
            data: xAxisData,
            axisLine: { onZero: true },
            splitLine: { show: false },
            splitArea: { show: false },
            axisTick: {
                show: false
            },
            axisLabel: {
                show: true,
                fontSize: 16,
                margin: 20,
                rotate: 90
            }
        },
        yAxis: {
            axisLabel: {
                show: true,
                fontSize: 16,
                margin: 6
            },
            splitLine: {
                show: false
            },
            axisLine: {
                show: true,
            },
            axisTick: {
                show: true
            }
        },
        grid: {
            bottom: 150
        },
        series: [
            {
                name: 'rural',
                type: 'bar',
                stack: 'one',
                barWidth: '50%',
                itemStyle: {
                    color: '#1A8162',
                    shadowBlur: 4,
                    shadowColor: 'rgba(0,0,0,0.1)'
                },
                data: data1
            },
            {
                name: 'urbano',
                type: 'bar',
                stack: 'one',
                barWidth: '50%',
                itemStyle: {
                    color: '#53EFBF',
                    shadowBlur: 5,
                    shadowColor: 'rgba(0,0,0,0.1)'
                },
                data: data2
            }
        ]
    };

    return (
        <div className='bar'>
            <div className="vertical mx-2">Total das gestantes do município</div>

            <ReactECharts
                option={options}
                style={{
                    width: "100%",
                    maxWidth: "500px",
                    minWidth: "370px",
                    height: "484px"
                }}
                opts={{ renderer: 'svg' }}
            />
        </div>
    )
}
