import { BrowserRouter, Routes, Route } from "react-router-dom";
import { AuthProvider } from './context/AuthProvider';
import { ProtectedLayout } from "./components/ProtectedLayout";

import { Login } from './pages/Login';
import { Painel } from './pages/Painel';
import { Gestante } from './pages/Gestante';

import { SelecionarUbs } from './pages/SelecionarUbs';

function App() {
  return (
    <AuthProvider>
      <BrowserRouter>
        <Routes>
          <Route path="/" element={<Login />} />

          <Route
            path="/selecionarubs"
            element={
              <ProtectedLayout>
                <SelecionarUbs />
              </ProtectedLayout>
            }
          />
          <Route
            path="/painelx"
            element={
              <ProtectedLayout>
                <Painel />
              </ProtectedLayout>
            }
          />
          <Route
            path="/painel/:id"
            element={
              <ProtectedLayout>
                <Painel />
              </ProtectedLayout>
            }
          />
          <Route
            path="/gestante/:id"
            element={
              <ProtectedLayout>
                <Gestante />
              </ProtectedLayout>
            }
          />
        </Routes>
      </BrowserRouter>
    </AuthProvider>
  );
}

export default App;
