import ReactECharts from 'echarts-for-react'
import { useEffect, useState } from 'react';
import { getPorcentagemIndicador } from '../../utils'
import './style.scss';

export function Pie({ data }: any) {
    let nome = data.indicador;
    const [porcentagem, setPorcentagem] = useState('');
    const [com_consulta, total] = [data].reduce(
        ([com_consulta, total]: any, d: { com_consulta: any; sem_consulta: any; }) => [
            com_consulta + d.com_consulta,
            (total + (d.com_consulta + d.sem_consulta))
        ],
        [0, 0]
    );

    let arrData = [data].map((item: { com_consulta: any; sem_consulta: any; }) => {
        return [
            { value: item.com_consulta },
            { value: item.sem_consulta }
        ]
    });

    useEffect(() => {
        let valor = getPorcentagemIndicador('', total, com_consulta);
        setPorcentagem(valor);
        return () => {
            console.log('...');
        }
    }, [com_consulta, total]);

    const options = {
        color: ['#361949', '#53EFBF'],
        tooltip: {
            trigger: "item",
            formatter: "({d}%)"
        },
        series: [
            {
                name: nome,
                type: "pie",
                radius: "80%",
                center: ["50%", "50%"],
                animationDuration: 5000,
                label: {
                    show: true,
                    position: 'center',
                    fontSize: '16',
                    fontWeight: 'bold'
                },
                itemStyle: {
                    shadowBlur: 10,
                    shadowOffsetX: 0,
                    shadowColor: "rgba(0, 0, 0, 0.5)"
                },
                labelLine: {
                    show: false
                },
                data: arrData[0]
            }
        ]
    };

    return (
        <div className='pie'>
            <ReactECharts
                option={options}
                style={{
                    width: "126px",
                    height: "126px"
                }}
                opts={{ renderer: 'svg' }}
            />
            <span className='porcentagem'>{porcentagem}</span>
            <span className='nomeGrafico'>{data.indicador}</span>
        </div>
    )
}
